.HomeView {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }

.HomeView__search {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500; }

.HomeView__main {
  height: 100%;
  overflow-y: auto; }

.HomeView__aside {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0; }

.HomeView__aside__content {
  height: 100%;
  width: 400px;
  margin: 0 auto; }

.HomeView__aside__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.85);
  z-index: 50; }
  .searchDropdownTransition-enter .HomeView__aside__overlay {
    opacity: 0; }
  .searchDropdownTransition-enter-active .HomeView__aside__overlay {
    opacity: 1;
    -webkit-transition: opacity 250ms ease;
    -o-transition: opacity 250ms ease;
    transition: opacity 250ms ease; }
  .searchDropdownTransition-leave .HomeView__aside__overlay {
    opacity: 1; }
  .searchDropdownTransition-leave-active .HomeView__aside__overlay {
    opacity: 0;
    -webkit-transition: opacity 250ms ease;
    -o-transition: opacity 250ms ease;
    transition: opacity 250ms ease; }



