.DropdownSubviewArtist {
  height: 100%;
  overflow-y: auto;
  padding-bottom: 90px; }

.DropdownSubviewArtist__header {
  background-color: #F0F0F0;
  padding: 90px 20px 20px 20px; }

.DropdownSubviewArtist__header__title {
  font-size: 20px;
  line-height: 24px; }

.DropdownSubviewArtist__body {
  margin-top: 40px; }

.DropdownSubviewArtist__topTracks__title {
  font-size: 20px;
  line-height: 24px;
  padding: 0 20px 10px 20px; }

.DropdownSubviewArtist__albums {
  margin-top: 40px; }

.DropdownSubviewArtist__albums__title {
  padding: 0 20px; }



